import { Injectable } from '@angular/core';

export interface IContact {
    name: string;
    number: string;
    popular: boolean;
}

@Injectable()
export class ContactsService {

    private readonly contacts: IContact[] = [
        { name: 'nischay', number: '9108558715', popular: true },
        { name: 'xyz', number: '448877996', popular: false },
        { name: 'wer', number: '455887744', popular: true },
        { name: 'jason', number: '5551131313', popular: false }
    ];
    getContacts(): IContact[] {
        return this.contacts;
    }
    getPopularContacts(): IContact[] {
        return this.contacts.filter((contact: IContact) => {
          return (contact.popular === true);
        });
      }
}